import {Component, OnInit, ViewChild, ElementRef, Input, Renderer2} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {ToastController} from '@ionic/angular';
import {CartItem} from 'src/app/client1/models/cart-item.model';
import {Food} from 'src/app/client1/models/food.model';
import {CartService} from 'src/app/client1/services/cart.service';
import {FoodAdminService} from 'src/app/client1/services/food-admin.service';
import {DataUrl, NgxImageCompressService} from 'ngx-image-compress';

@Component({
  selector: 'app-edit-food',
  templateUrl: './edit-food.page.html',
  styleUrls: ['./edit-food.page.scss'],
})
export class EditFoodPage implements OnInit {
  @ViewChild('imageElement', { static: false }) imageElement: ElementRef;
  @Input() maxWidth = 400;
  @Input() maxHeight = 400;
  id: number;
  food: Food;
  foood: Food;
  fod: Food[];
  message = '';
  imgResultAfterResizeMax: DataUrl = '';
  timestamp: number;
  imageError = false;

  constructor(
    private activatedRoute: ActivatedRoute,
    private foodService: FoodAdminService,
    private cartService: CartService,
    private toastCtrl: ToastController,
    private router: Router,
    private route: ActivatedRoute,
    private imageCompress: NgxImageCompressService,
    private el: ElementRef, private renderer: Renderer2
  ) {
    this.id = +this.activatedRoute.snapshot.paramMap.get('id');
    this.timestamp = Date.now();
  }

  ngOnInit() {
   this.getFoodById(this.id);
   //this.imgResultAfterResizeMax=this.food.image;
  }

  getFoodById(id: number): void {
    this.foodService.getFoodsById(this.id)
      .subscribe(
        data => {
          this.food = data;
          console.log(data);
        },
        error => {
          console.log(error);
        });
  }

  addItemToCart() {
    const cartitem: CartItem = {
      id: this.food.id,
      name: this.food.title,
      price: this.food.price,
      image: this.imgResultAfterResizeMax,
      quantity: 1,
    };

    this.cartService.addToCart(cartitem);
    this.presentToast();
  }

  async presentToast() {
    const toast = await this.toastCtrl.create({
        message: 'Food added to the cart',
        mode: 'ios',
        duration: 1000,
        position: 'bottom',
        color: 'success'
      }
    );
    toast.present();
    // @ts-ignore
    this.router.navigate(['/admin/listing']);
  }

  async confirmationEdit() {
    const toast = await this.toastCtrl.create({
        message: 'Updated successfully!',
        mode: 'ios',
        duration: 1000,
        position: 'bottom',
        color: 'success'
      }
    );
    toast.present();
    this.router.navigate(['/admin/listing']);
  }



  uploadAndReturnWithMaxSize() {
    return this.imageCompress.uploadAndGetImageWithMaxSize(1, true).then(
      (result: DataUrl) => {
        this.imgResultAfterResizeMax = result;
        console.log('-->', result);
      },
      (result: string) => {
        console.error(
          'The compression algorithm didn\'t succed! The best size we can do is',
          this.imageCompress.byteCount(result),
          'bytes'
        );
        this.imgResultAfterResizeMax = result;
      }
    );
  }

  updateFood(): void {
    const data = {
      title: this.food.title,
      price: this.food.price,
      image: this.imgResultAfterResizeMax,
      description: this.food.description,
      published: this.food.published,
    };
    this.foodService.update(this.id, data)
      .subscribe(
        response => {
          console.log(response);
          this.message = response.message ? response.message : 'This element was updated successfully!';
          this.confirmationEdit();
          window.location.reload();
        },
        error => {
          console.log(error);
        });
    this.router.navigate(['/admin/listing']);
  }


  checkImageDimensions() {
    const imageElement: HTMLImageElement = this.imageElement.nativeElement;
    if (imageElement.height > 400 || imageElement.height > 400 ) {
     // alert('l\'image est trop grand, Veuillez choisir la hauteur Max 400px et largeur Max 400px');
      this.imageError = true;
    }
  }



}
